html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

progress {
  vertical-align: baseline; }

template,
[hidden] {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline-width: 0; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: inherit; }

b,
strong {
  font-weight: bolder; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

button,
input,
select,
textarea {
  font: inherit; }

optgroup {
  font-weight: bold; }

button,
input,
select {
  overflow: visible; }

button,
input,
select,
textarea {
  margin: 0; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  cursor: pointer; }

[disabled] {
  cursor: default; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button:-moz-focusring,
input:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

html {
  font-size: 62.5%; }

body {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: normal;
  background: #fff;
  color: #555;
  line-height: 2;
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  @media screen and (min-width: 768px) {
    body {
      min-width: 768px; } }

main {
  flex: 1 0 auto; }

a {
  color: #2e5e86;
  text-decoration: none; }
  a:visited {
    color: #2e5e86; }
  a:hover, a:active, a:focus {
    color: #3b79ac; }

::selection {
  background: #b3d4fc; }

ul li,
ol li {
  margin-top: 10px; }

dl {
  margin: 20px 0 10px; }
  dl dd {
    margin: 0.2em 0 1.2em;
    padding: 0; }
    dl dd:last-of-type {
      margin-bottom: 0; }

h1,
h2,
h3,
h4 {
  border-bottom: 1px solid #eaeaea; }

h1 {
  font-size: 28px;
  font-size: 2.8rem; }
  @media screen and (min-width: 768px) {
    h1 {
      font-size: 32px;
      font-size: 3.2rem; } }

h2 {
  font-size: 24px;
  font-size: 2.4rem; }

h3 {
  font-size: 20px;
  font-size: 2rem; }

h4 {
  font-size: 16px;
  font-size: 1.6rem; }

tt,
code,
kbd,
pre,
samp {
  font-family: "Roboto Mono", monospace;
  color: #555;
  background: #fafafa;
  border-radius: 3px;
  padding: 0.5em; }

ul {
  padding-left: 20px; }

p:first-child {
  margin-top: 30px; }

.container:before, .container:after {
  content: "";
  display: table; }

.container:after {
  clear: both; }

@media screen and (min-width: 768px) {
  .container {
    margin: 0 auto;
    max-width: 1400px;
    padding: 0 4.16667%; } }

.navbar {
  background: #fff;
  border-bottom: 1px solid #eaeaea;
  overflow: hidden;
  padding: 4px 0;
  position: relative;
  z-index: 0; }
  .navbar:before, .navbar:after {
    content: "";
    display: table; }
  .navbar:after {
    clear: both; }
  @media screen and (min-width: 768px) {
    .navbar {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100; } }
  .navbar .meta {
    font-size: 13px;
    font-size: 1.3rem;
    color: #888;
    font-weight: bold;
    margin: 4px 0;
    display: none; }
    @media screen and (min-width: 1150px) {
      .navbar .meta {
        float: left;
        width: 200px;
        display: block; } }
  .navbar .logo {
    font-size: 40px;
    font-size: 4rem;
    background: url(../img/logo.png) 0 0 no-repeat;
    color: #2e5e86;
    display: block;
    float: left;
    font-weight: bold;
    margin: 10px;
    overflow: hidden;
    text-decoration: none;
    text-indent: 100%;
    height: 40px; }
    @media screen and (min-width: 768px) {
      .navbar .logo {
        margin-left: 0; } }
  .navbar .menu-button {
    font-size: 20px;
    font-size: 2rem;
    color: #2e5e86;
    cursor: pointer;
    display: block;
    float: right;
    height: 45px;
    margin: 4px 10px;
    text-align: center;
    text-decoration: none;
    width: 45px; }
    @media screen and (min-width: 768px) {
      .navbar .menu-button {
        display: none; } }
    .navbar .menu-button:active, .navbar .menu-button.active {
      color: #5592c5; }
  .navbar .nav-menu {
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease-out;
    width: 100%; }
    @media screen and (min-width: 768px) {
      .navbar .nav-menu {
        max-height: none;
        transition: none;
        width: auto;
        float: right; } }
    .navbar .nav-menu.active {
      max-height: 500px; }
    .navbar .nav-menu ul {
      margin: 10px 0 0;
      padding: 0; }
      @media screen and (min-width: 768px) {
        .navbar .nav-menu ul {
          margin: 0; } }
    .navbar .nav-menu li {
      font-size: 13px;
      font-size: 1.3rem;
      display: block;
      font-weight: bold;
      line-height: 16px;
      text-align: left;
      text-transform: uppercase;
      margin: 0 10px;
      border-top: 1px solid #eaeaea; }
      @media screen and (min-width: 768px) {
        .navbar .nav-menu li {
          margin: 0;
          border: 0;
          float: left;
          text-align: center; } }
      .navbar .nav-menu li.active a {
        color: #3b79ac; }
    .navbar .nav-menu a {
      color: #2e5e86;
      display: block;
      padding: 20px 0;
      text-decoration: none; }
      .navbar .nav-menu a:active, .navbar .nav-menu a:hover {
        color: #3b79ac; }
      @media screen and (min-width: 768px) {
        .navbar .nav-menu a {
          padding: 20px 10px; } }
